import React, { memo } from 'react';
import { Select } from 'antd';
import { I_Scene } from '@containers/Home/types';
import { SelectOptionWithImageWrapper } from './styled';

interface Props {
  value: string;
  className?: string;
  onChange: (value: string) => void;
  listScene: I_Scene[];
}

const SelectScene = memo(({ listScene, className, value, onChange }: Props) => {
  return (
    <Select
      value={value}
      bordered={false}
      onChange={onChange}
      showSearch
      placeholder="Select scene"
      optionFilterProp="label"
      optionLabelProp="label"
      filterOption={(input, option: any) =>
        input.toLowerCase()
          ? option.label &&
            option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0
          : option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0
      }
      className={`${className || ''}`}
    >
      {listScene.map(item => (
        <Select.Option key={item.id} value={item.id} label={item.title}>
          <SelectOptionWithImageWrapper>
            <img src={item.media?.src_thumb || ''} alt={item.title} />
            <span className="text-ellipsis">{item.title}</span>
          </SelectOptionWithImageWrapper>
        </Select.Option>
      ))}
    </Select>
  );
});

export default SelectScene;
